<template>
    <div id="page">
                    <h1 class="main-title">Check the weather in any city of the world</h1>
                <div class="input-container">
          <input placeholder="Enter a city..." spellcheck="false" class="city-input" type="text" v-model="inputCity" v-on:keydown.enter="getApi(); titleCity()">
          <button class="city-input__button" @click="getApi(); titleCity()">Check</button>
          </div>
              <span class="city-info" v-if="targetCity">Weather in <br> <span v-if="targetCity" class="target-city">{{targetCity}}, {{countryOfCity}}</span></span>
              <div class="data__weather data-item" v-if="targetCity"><span class="data-value">{{weather}}</span></div>
          <div class="data__container" v-if="targetCity">
              <div class="data-main__container">
              <div class="data__degrees">
                  <span class="degrees_true data-item">Temperature <br><span class="data-value">{{degreesTrue}}</span><span class="data-unit" v-if="degreesTrue">°</span></span>
                  <span class="degrees_feels-like data-item">Feels like<br><span class="data-value">{{degreesFeelsLike}}</span><span class="data-unit" v-if="degreesFeelsLike">°</span></span>
              </div>
              <div class="data__weather-container">
              <div class="data__wind data-item">Wind <br> <span class="data-value">{{wind}}</span><span class="data-unit" v-if="wind"> m/s</span></div>
              </div>
          </div>
              <div class="data-other__container">
              <div class="pressure data-item"> Pressure <br> <span class="data-value">{{pressure}}</span> <span class="data-unit" v-if="pressure">mmHg</span></div>
              <div class="humidity data-item">Humidity <br> <span class="data-value">{{humidity}}</span> <span class="data-unit" v-if="humidity">%</span></div>
              </div>
              </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            citySelected:'1',
            errors:[],
            inputCity: '',
            targetCity: '',
            countryOfCity: '',
            degreesTrue: '',
            degreesFeelsLike:'',
            weather: '',
            wind: '',
            pressure:'',
            humidity:'',
        }
    },
    methods: {
            capitalizeFirstLetter(string) {
            return string.charAt(0).toUpperCase() + string.slice(1);
            },
            async getApi () {
            try {
                this.inputCity = this.capitalizeFirstLetter(this.inputCity);
                const cityName = this.inputCity;
                const response = await fetch (`https://api.openweathermap.org/data/2.5/weather?q=${cityName}&appid=b20710ae17fb00084885578609ebaba4&units=metric`);
                const city = await response.json();
                console.log (city)
                this.countryOfCity = city.sys.country;
                this.degreesTrue = city.main.temp;
                this.degreesFeelsLike = city.main.feels_like;
                this.weather = city.weather[0].description;
                this.wind = city.wind.speed;
                this.pressure = city.main.pressure;
                this.humidity = city.main.humidity;

            } catch (error) {
                this.errors.push(error);
                console.log('Errors:' + this.errors)
            }
        },
        titleCity () {
            this.targetCity = this.inputCity;
            this.inputCity = '';
        }
    }
}
</script>


<style scoped>
@font-face {
    font-family: 'tt-hoves';
    src: url(../assets/TTHoves-Regular.ttf);
}


#page {
    font-family: tt-hoves;
background: rgb(232, 235, 255);
background: linear-gradient(45deg, rgba(23, 50, 202, 0.904) 0%, rgba(255, 187, 1, 0.7) 100%);
    height:900px;
    overflow-y:hidden;
    padding-top:35px;
    margin:0 auto;
    display:flex;
    flex-direction: column;
    align-items:center;
}

.main-title {
    color:rgb(31, 20, 0);
    font-size: 26px;
}

.input-container {
    display:flex;
    margin-bottom: 40px;

}

.city-input {
    font-family: tt-hoves;
    font-size:22px;
    color:rgb(0, 71, 224);
    padding-left:10px;
    height:40px;
    width:320px;
    border-radius: 0;
    border:none;
}

.city-input:focus {
    outline:none;

}

::placeholder {
    color:rgba(0, 71, 224,0.5);
}

.city-input:focus::placeholder {
    color:transparent;
    
}

.city-input__button {
    font-family: tt-hoves;
    font-size: 22px;
    width:120px;
    background-color: rgb(255, 188, 64);
    color:rgb(0, 71, 224);
    height:42px;
    border-radius: 0;
    border:none;
    transition-duration: 0.15s;
}

.city-input__button:focus {
    outline:none;
}

.city-input__button:hover {
    background-color: rgb(219, 124, 0);
    color:rgb(181, 204, 255);
}


.city-info {
    text-align: center;
    font-size:20px;
    margin-bottom:40px;
}

.target-city {
    font-weight: bold;
    font-size: 32px;
}

.data-item {
    text-align:center;
}

.data__container {
    width:400px;
    height:400px;
    display:flex;
    flex-direction: column;
    align-items: center;
}

.data-main__container {
    display:flex;
    flex-direction: column;
    align-items:center;
    margin-bottom: 40px;
}

.data__degrees {
    display:flex; 
    width:400px;
    justify-content: space-between;
    margin-bottom: 40px;
}

.data__weather {
    margin-bottom: 60px;
}

.degrees_feels-like {
    position:relative;
    left:-25px;
}


.data__weather-container {
    width:400px;
    display:flex;
    justify-content: center;
}

.data-other__container {
    width:400px;
    display:flex;
    justify-content: center;
}

.data-other__container > .data-item {
    padding-left:70px;
    padding-right:70px;
}

.data-value {
    font-size: 30px;
    font-weight:bold;
}

.data-item {
    font-size:24px;
}

.data-unit {
